
@mixin extra-small {
    @media (min-width: 429px) { @content }
}

@mixin small {
    @media (min-width: 600px) { @content }
}

@mixin medium {
    @media (min-width: 768px) { @content }
}

@mixin large {
    @media (min-width: 992px) { @content }
}

@mixin extra-large {
    @media (min-width: 1200px) { @content }
}
